import React from 'react'

import Sekcio from '../Aferoj/Sekcio'
import cxefoj from '../Okazejoj'

import { Row, Col } from 'react-bootstrap'

export default class Hejmo extends React.Component {
  constructor(props) {
    super(props)
  }
  render() {
    return (
      <>
        <Sekcio dark={true}>
          <Row>
            <Col>
              <h2>{cxefoj.KialEsperanto}</h2>
              <p className='justify'>{cxefoj.Citajxo}</p>
              <p style={{ textAlign: 'center' }}>-- L. L. Zamenhof, 1912</p>
            </Col>
            <Col>
              <iframe
                id='ytplayer'
                type='text/html'
                width='640'
                height='360'
                src={
                  `https://www.youtube.com/embed/8gSAkUOElsg?autoplay=0` +
                  `&cc_lang_pref=${cxefoj.getLanguage()}` +
                  `&hl=${cxefoj.getLanguage()}` +
                  `${cxefoj.getLanguage() != 'en' ? '&cc_load_policy=1' : ''}`
                }
                frameborder='0'
              />
            </Col>
          </Row>
        </Sekcio>
        <Sekcio dark={false}>test</Sekcio>
      </>
    )
  }
}
